* {
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
}

html {
	background-color: #353535;
}

body {
	background-color: #fff;
	margin: 0;
	padding: 0;
}

img {
	width: 100%;
}

h1,
h2,
h3,
h4,
p {
	color: #fff;
	font-weight: 100;
	margin: 0;
	padding: 0;
}

a {
	color: #e3cfa8;
	text-decoration: none;
	transition: all 0.2s linear;
}

a:hover {
	color: #ffe7b9;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

.clear:after {
	content: "";
	display: table;
	clear: both;
}